"use client";

import { useState } from "react";
import Image from "next/image";
import { MapPin, Clock } from "lucide-react";

const ShoppingInfoComp = () => {
  const [selectedDelivery, setSelectedDelivery] = useState("pickup1");
  const [saveInfo, setSaveInfo] = useState(false);
  const [setAsDefault, setSetAsDefault] = useState(false);
  const [confirmDetails, setConfirmDetails] = useState(false);

  return (
    <div className="py-8">
      <div className="container mx-auto">
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
          {/* Left Column */}
          <div className="lg:col-span-2">
            <h1 className="text-3xl font-bold mb-6">Shipping Information</h1>

            {/* Saved Address */}
            <div className="bg-gray-50 rounded-2xl p-6 mb-6">
              <div className="flex justify-between mb-4">
                <h3 className="font-semibold">Saved Shipping Information</h3>
                <button className="text-amber-600 text-sm font-medium">
                  Change Address
                </button>
              </div>

              <p className="font-semibold">Otor John Stephen</p>
              <p className="text-gray-600">
                38 Asheli, James Street, Jabi Abuja
              </p>

              <div className="flex gap-4 text-sm text-gray-600">
                <span>myemail@address@gmail.com</span>
                <span>08130000000</span>
              </div>

              <button className="text-blue-600 text-sm flex items-center gap-1 mt-2">
                <MapPin className="w-4 h-4" />
                Default address
              </button>
            </div>

            {/* New Shipping Form */}
            <div className="bg-white rounded-2xl border p-6 mb-6">
              <h3 className="font-semibold mb-4">
                OR Enter another shipping information
              </h3>

              <div className="grid grid-cols-2 gap-4 mb-4">
                <input placeholder="First name" className="input" />
                <input placeholder="Last name" className="input" />
              </div>

              <div className="grid grid-cols-2 gap-4 mb-4">
                <input placeholder="Email address" className="input" />
                <input placeholder="Phone number" className="input" />
              </div>

              <input placeholder="Street address" className="input mb-4" />

              <div className="flex gap-6">
                <label className="flex gap-2 text-sm">
                  <input
                    type="checkbox"
                    checked={saveInfo}
                    onChange={(e) => setSaveInfo(e.target.checked)}
                  />
                  Save this information
                </label>

                <label className="flex gap-2 text-sm">
                  <input
                    type="checkbox"
                    checked={setAsDefault}
                    onChange={(e) => setSetAsDefault(e.target.checked)}
                  />
                  Set as default
                </label>
              </div>
            </div>

            {/* Delivery Method */}
            <div className="bg-white rounded-2xl border p-6 mb-6">
              <h2 className="text-xl font-bold mb-4">Delivery Method</h2>

              <button
                onClick={() => setSelectedDelivery("pickup1")}
                className={`w-full p-4 rounded-xl border-2 mb-3 ${
                  selectedDelivery === "pickup1"
                    ? "border-amber-500 bg-amber-50"
                    : "border-gray-200"
                }`}
              >
                <div className="flex justify-between">
                  <span className="font-semibold">FLEZTA Pickup Station</span>
                  <span className="text-xs bg-amber-500 text-white px-2 py-1 rounded">
                    $5.99
                  </span>
                </div>

                <div className="flex gap-2 text-sm text-gray-600 mt-2">
                  <MapPin className="w-4 h-4" />
                  This will be the location
                </div>

                <div className="flex gap-2 text-sm text-gray-600">
                  <Clock className="w-4 h-4" />
                  Mon–Fri, 8am–6pm
                </div>
              </button>
            </div>

            {/* Confirmation */}
            <label className="flex gap-2 text-sm">
              <input
                type="checkbox"
                checked={confirmDetails}
                onChange={(e) => setConfirmDetails(e.target.checked)}
              />
              Confirm delivery details before payment.
            </label>
          </div>

          {/* Right Column */}
          <div className="lg:col-span-1">
            <div className="bg-white rounded-2xl border p-6 sticky top-6">
              <h2 className="text-xl font-bold mb-6">Cart Summary</h2>

              <div className="space-y-4">
                <div className="flex justify-between">
                  <span>Items total</span>
                  <span className="font-semibold">$1,950.00</span>
                </div>

                <div className="border-t pt-4 flex justify-between text-lg font-bold">
                  <span>Total</span>
                  <span>$1,905.99</span>
                </div>
              </div>

              <button className="w-full mt-6 bg-emerald-900 text-white py-4 rounded-xl">
                Proceed To Payment
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ShoppingInfoComp;
